import Vue from 'vue'
import Vuex from 'vuex'
import type from '@/mutations'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    version: '0.1',
    app_name: 'Andriawan Projects',
    author: 'Andriawan',
    final_display: '0',
    temp_data: '',
    result: '',
    active_arithmetic_state: 'init'
  },
  mutations: {
    [type.OPERATION_STATE] (state, operation_type) {
      state.active_arithmetic_state = operation_type
      if (state.result !== '') {
        state.temp_data = parseFloat(state.result)
        state.result = ''
      }
    },
    [type.SET_RESULT] (state) {
      if (state.result != null) {
        switch (state.active_arithmetic_state) {
          case 'multi':
            state.result = state.result * state.temp_data
            break
          case 'divi':
            state.result = state.temp_data / state.result
            break
          case 'add':
            state.result = state.temp_data + parseFloat(state.result)
            break
          case 'subtract':
            state.result = state.temp_data - parseFloat(state.result)
            break
          default:
            state.result = ''
        }

        state.final_display = state.result
        state.temp_data = parseFloat(state.result)
        state.result = ''
        state.active_arithmetic_state = 'init'
      }
    },
    [type.CLEAR_SCREEN] (state) {
      state.final_display = '0'
      state.temp_data = 0
      state.result = ''
      state.active_arithmetic_state = 'init'
    },
    [type.SET_SCREEN] (state, value) {
      state.result = state.result + value
      state.final_display = state.result
    }
  },
  actions: {
    change_op_state ({ commit }, operation_type) {
      commit(type.OPERATION_STATE, operation_type)
    },
    clear_screen ({ commit }) {
      commit(type.CLEAR_SCREEN)
    },
    set_screen ({ commit }, value) {
      commit(type.SET_SCREEN, value)
    },
    set_result ({ commit }) {
      commit(type.SET_RESULT)
    }
  }
})
