<template>
  <div>
    <input
      class="appearance-none h-full text-right text-5xl md:text-4xl bg-black text-green-400 border rounded w-full py-2 px-3 md:h-20 text-gray-700 leading-tight focus:outline-none"
      id="screen"
      :value="display"
      type="text"
      placeholder="0"
    >
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState({
      display: state => state.final_display
    })
  }
};
</script>