<template>
  <div class="flex flex-col">
    <div class="text-gray-700 h-full text-center">
      <div class="flex h-full">
        <div class="w-1/4 text-center">
          <button
            :class="active_arithmetic_state('multi')"
            @click="change_op_state('multi')"
            class="w-full h-full md:h-16 text-4xl bg-blue-500 hover:bg-blue-400 text-white font-bold"
          >X</button>
        </div>
        <div class="w-1/4 text-center">
          <button
            :class="active_arithmetic_state('divi')"
            @click="change_op_state('divi')"
            class="w-full h-full md:h-16 text-4xl bg-blue-500 hover:bg-blue-400 text-white font-bold"
          >/</button>
        </div>
        <div class="w-1/4 text-center">
          <button
            :class="active_arithmetic_state('add')"
            @click="change_op_state('add')"
            class="w-full h-full md:h-16 text-4xl bg-blue-500 hover:bg-blue-400 text-white font-bold"
          >+</button>
        </div>
        <div class="w-1/4 text-center">
          <button
            :class="active_arithmetic_state('subtract')"
            @click="change_op_state('subtract')"
            class="w-full h-full md:h-16 text-4xl bg-blue-500 hover:bg-blue-400 text-white font-bold"
          >-</button>
        </div>
      </div>
    </div>
    <div class="text-gray-700 h-full">
      <div class="flex h-full">
        <div class="w-1/3 text-center">
          <button
            @click="set_screen(1)"
            class="w-full h-full md:h-16 text-4xl bg-green-500 hover:bg-green-400 text-white font-bold"
          >1</button>
        </div>
        <div class="w-1/3 text-center">
          <button
            @click="set_screen(2)"
            class="w-full h-full md:h-16 text-4xl bg-green-500 hover:bg-green-400 text-white font-bold"
          >2</button>
        </div>
        <div class="w-1/3 text-center">
          <button
            @click="set_screen(3)"
            class="w-full h-full md:h-16 text-4xl bg-green-500 hover:bg-green-400 text-white font-bold"
          >3</button>
        </div>
      </div>
    </div>
    <div class="text-gray-700 h-full">
      <div class="flex h-full">
        <div class="w-1/3 text-center">
          <button
            @click="set_screen(4)"
            class="w-full h-full md:h-16 text-4xl bg-green-500 hover:bg-green-400 text-white font-bold"
          >4</button>
        </div>
        <div class="w-1/3 text-center">
          <button
            @click="set_screen(5)"
            class="w-full h-full md:h-16 text-4xl bg-green-500 hover:bg-green-400 text-white font-bold"
          >5</button>
        </div>
        <div class="w-1/3 text-center">
          <button
            @click="set_screen(6)"
            class="w-full h-full md:h-16 text-4xl bg-green-500 hover:bg-green-400 text-white font-bold"
          >6</button>
        </div>
      </div>
    </div>
    <div class="text-gray-700 h-full">
      <div class="flex h-full">
        <div class="w-1/3 text-center">
          <button
            @click="set_screen(7)"
            class="w-full h-full md:h-16 text-4xl bg-green-500 hover:bg-green-400 text-white font-bold"
          >7</button>
        </div>
        <div class="w-1/3 text-center">
          <button
            @click="set_screen(8)"
            class="w-full h-full md:h-16 text-4xl bg-green-500 hover:bg-green-400 text-white font-bold"
          >8</button>
        </div>
        <div class="w-1/3 text-center">
          <button
            @click="set_screen(9)"
            class="w-full h-full md:h-16 text-4xl bg-green-500 hover:bg-green-400 text-white font-bold"
          >9</button>
        </div>
      </div>
    </div>
    <div class="text-gray-700 h-full">
      <div class="flex h-full">
        <div class="w-full text-center">
          <button
            @click="set_screen(0)"
            class="w-full h-full md:h-16 text-4xl bg-green-500 hover:bg-green-400 text-white font-bold"
          >0</button>
        </div>
      </div>
    </div>
    <div class="text-gray-700 h-full">
      <div class="flex h-full">
        <div class="w-1/2 text-center">
          <button
            @click="set_result"
            class="w-full h-full md:h-16 text-4xl bg-blue-500 hover:bg-blue-400 text-white font-bold"
          >=</button>
        </div>
        <div class="w-1/2 text-center">
          <button
            @click="clear_screen"
            class="w-full h-full md:h-16 text-4xl bg-red-600 hover:bg-red-400 text-white font-bold"
          >C</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { mapActions } from "vuex";
export default {
  methods: {
    active_arithmetic_state(op_type) {
      if (this.operation_state == op_type) return { "bg-blue-700": true };
    },
    ...mapActions({
      change_op_state: "change_op_state",
      clear_screen: "clear_screen",
      set_screen: "set_screen",
      set_result: "set_result"
    })
  },
  computed: {
    ...mapState({
      operation_state: state => state.active_arithmetic_state
    })
  }
};
</script>